.experience-container{padding:4rem 3rem;background:linear-gradient(135deg, #0e0125 0%, #1a0b3a 50%, #2d1b69 100%);min-height:70vh}.section-title{text-align:center;color:#f7f7f7;font-family:"Mozilla Headline",sans-serif;font-size:2.5rem;font-weight:700;margin-bottom:3rem;letter-spacing:-0.02em;background:linear-gradient(45deg, #f7f7f7 0%, #e0e0e0 100%);-webkit-background-clip:text;-webkit-text-fill-color:rgba(0,0,0,0);background-clip:text;position:relative}.section-title::after{content:"";position:absolute;bottom:-1rem;left:50%;transform:translateX(-50%);width:60px;height:3px;background:linear-gradient(135deg, #8b5cf6 0%, #a855f7 50%, #c084fc 100%);border-radius:6px}.experience-content{max-width:1200px;margin:0 auto;display:flex;flex-direction:column;gap:2rem;align-items:center}.companies-menu{display:flex;gap:1.5rem;justify-content:center;flex-wrap:wrap}.company-item{padding:1rem 1.5rem;background:rgba(0,0,0,0);border:none;border-radius:0;cursor:pointer;transition:all .3s ease;width:180px;height:80px;display:flex;flex-direction:column;justify-content:center;text-align:left;position:relative}.company-item:hover{background:rgba(139,92,246,.05)}.company-item::before{content:"";position:absolute;left:0;top:0;width:3px;height:100%;background:rgba(0,0,0,0);transition:background .3s ease}.company-item.active::before{background:#8b5cf6}.company-item h3{color:#fff;font-family:"Mozilla Headline",sans-serif;font-size:1rem;font-weight:500;margin:0 0 .3rem 0;letter-spacing:-0.01em;line-height:1.2}.company-item.active h3{color:#8b5cf6;font-weight:600}.company-item .role{color:#fff;font-size:.875rem;margin:0 0 .2rem 0;opacity:.8;font-family:"Inter",-apple-system,BlinkMacSystemFont,sans-serif;line-height:1.3}.company-item .period{color:#fff;font-size:.75rem;margin:0;opacity:.6;font-family:"Inter",-apple-system,BlinkMacSystemFont,sans-serif;font-weight:500}.experience-details{width:100%;max-width:800px;padding:3rem;background:rgba(0,0,0,0);border:none;border-radius:0;min-height:300px}.selected-experience h3{color:#f7f7f7;font-family:"Mozilla Headline",sans-serif;font-size:2rem;font-weight:700;margin:0 0 1.5rem 0;letter-spacing:-0.02em;background:linear-gradient(45deg, #f7f7f7 0%, #e0e0e0 100%);-webkit-background-clip:text;-webkit-text-fill-color:rgba(0,0,0,0);background-clip:text}.selected-experience .description{color:#fff;font-size:1rem;line-height:1.7;margin-bottom:2rem;opacity:.9;font-family:"Inter",-apple-system,BlinkMacSystemFont,sans-serif}.activities h4{color:#f7f7f7;font-family:"Mozilla Headline",sans-serif;font-size:1.125rem;font-weight:600;margin:0 0 1.5rem 0;letter-spacing:-0.01em}.activities ul{list-style:none;padding:0;margin:0}.activities li{color:#fff;font-size:.875rem;line-height:1.5;margin-bottom:1rem;padding-left:1.5rem;position:relative;opacity:.8;font-family:"Inter",-apple-system,BlinkMacSystemFont,sans-serif}.activities li::before{content:"‚ñπ";color:#f7f7f7;position:absolute;left:0;font-weight:bold}@media(max-width: 768px){.experience-container{padding:3rem 2rem}.section-title{font-size:2rem;margin-bottom:2rem}.experience-content{gap:1.5rem}.companies-menu{gap:1rem}.experience-details{padding:2rem}.selected-experience h3{font-size:1.5rem}}